.genre_list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  width: 800px;
}

.genre_item {
  align-items: center;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background: #242424;
  border: 1px solid #454545;
  border-radius: 10px;
  color: var(--main-text-color);
  cursor: pointer;
  display: flex;
  font-size: 14px;
  font-weight: 800;
  height: 46px;
  justify-content: center;
  line-height: 143%;
  padding: 0 25px;
  text-transform: uppercase;
}

.genre_item_active {
  align-items: center;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: var(--main-green-color);
  border: 1px solid #454545;
  border-radius: 10px;
  color: var(--main-text-color);
  cursor: pointer;
  display: flex;
  font-size: 14px;
  font-weight: 800;
  height: 46px;
  justify-content: center;
  line-height: 143%;
  padding: 0 25px;
  text-transform: uppercase;
}
